
import Day from "@/types/Day";
import { defineComponent, PropType } from "vue";

export default defineComponent({
  props: {
    days: {
      required: true,
      type: Array as PropType<Day[]>,
    },
    date: {
      required: true,
      type: String as PropType<Day>,
    },
    changeDay: {
      required: true,
      type: Function as PropType<(day: Day) => void>,
    },
  },
  setup() {
    return {};
  },
});
